<template>
  <v-container fluid>
    <v-row class="v-row-image justify-center align-center text-center">
      <v-col class="mt-16">
        <div class="white--text text-center display-3 font-weight-bold ">
          WELLO
        </div>
        <div class="title-height " />
      </v-col>
    </v-row>
    <v-row class="mt-10">
      <v-col>
        <v-text-field
          v-model="form.username"
          label="Username"
          filled
          outlined
          :class="
            $vuetify.breakpoint.name === 'xl'
              ? 'text-block-l '
              : 'text-block-s '
          "
          :style="{ left: '50%', transform: 'translateX(-50%)' }"
        ></v-text-field>
        <v-text-field
          v-model="form.password"
          label="Password"
          filled
          outlined
          type="password"
          :class="
            $vuetify.breakpoint.name === 'xl'
              ? 'text-block-l '
              : 'text-block-s '
          "
          :style="{ left: '50%', transform: 'translateX(-50%)' }"
        >
        </v-text-field>
        <v-alert v-if="!!message" type="error">
          {{ message }}
        </v-alert>
        <v-btn
          depressed
          justify-center
          :class="
            $vuetify.breakpoint.name === 'xl'
              ? 'text-block-l '
              : 'text-block-s '
          "
          class="primary"
          :loading="loading"
          @click="submit()"
          :style="{ left: '50%', transform: 'translateX(-50%)' }"
          >sign in!</v-btn
        >

        <div class="text-normal">Forgot password</div>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
export default {
  data: () => ({
    form: {
      username: "",
      password: "",
    },
    loading: false,
    message: null,
  }),
  computed: {},
  methods: {
    async submit() {
      this.loading = true;
      try {
        let { data } = await this.$http.post("login", this.form);
        if (!data.status)
          return (this.message = this.$vuetify.lang.t(
            "$vuetify.efwb.loginfailcode." + data.reason
          ));
        this.$session.setSession(data.user);
        this.$router.push({
          name: "Band List",
        });
      } catch (error) {
        this.message = error.message;
      } finally {
        this.loading = false;
      }
    },
    clear() {
      this.name = "";
      this.password = "";
      this.select = null;
      this.checkbox = false;
    },
  },
  created() {
    if (this.$session.user != null)
      this.$router.push({
        name: "Band List",
      });
  },
};
</script>
<style lang="scss" scoped>
.text-normal {
  text-align: center;
  color: black;
}
.title-height {
  height: 15vh;
}

.v-row-image {
  background-image: url("../assets/login_background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.text-block-s {
  width: 100%;
  text-align: center;
}
.text-block-l {
  width: 500px;
  text-align: center;
}
.center-block-s {
  width: 100%;
}
.center-block-l {
  width: 500px;
}
</style>
