<template>
  <div>
    <ModalView
      :clearview="isModalViewed"
      v-if="isModalViewed"
      @close-modal="closeModal()"
      class="modalview"
    ></ModalView>
    <Alert
      :dialogCheck="alertView"
      @dialog-event="dialogEvent"
      :value="value"
    />

    <v-card flat height="120" class="primary rounded-0 mx-auto"> </v-card>
    <v-card
      flat
      width="100"
      height="100"
      class="center secondary mt-n13 mx-auto"
      ><v-icon size="85" color="black" class="icon">
        mdi-account-circle
      </v-icon>
    </v-card>
    <v-card flat>
      <v-card-text class="display-1 text-center font-weight-bold">
        {{ band.name }}
      </v-card-text>
    </v-card>
    <v-container fluid>
      <v-list>
        <div class="border-style-top rounded-t-lg">
          <v-list-item-title class="title-style-margin"
            > </v-list-item-title
          >
          <v-list-item>
            <v-list-item-content>
              <div class="mx-auto text-center">
                <v-avatar color="thirdly" size="90" class="mb-2">
                  <span class="text-h5 font-weight-bold">{{
                    banddata.bandData.hr
                  }}</span>
                </v-avatar>
                <v-list-item-title class="title-style"
                  > (BPM)</v-list-item-title
                >
              </div>
            </v-list-item-content>
            <v-list-item-content>
              <div class="mx-auto text-center">
                <v-avatar color="thirdly" size="90" class="mb-2">
                  <span
                    v-if="banddata.bandData.spo2 != '-'"
                    class="text-h5 font-weight-bold"
                    >{{ banddata.bandData.spo2 / 10 }}</span
                  >
                  <span v-else class="text-h5 font-weight-bold">-</span>
                </v-avatar>
                <v-list-item-title class="title-style"
                  > (%)</v-list-item-title
                >
              </div>
            </v-list-item-content>
          </v-list-item>
        </div>
        <div flat outlined class="border-style rounded-b-lg">
          <v-list-item-title class="title-style-margin"
            > </v-list-item-title
          >
          <v-list-item>
            <v-list-item-content>
              <div class="mx-auto text-center">
                <v-avatar
                  v-if="banddata.bandData.scdState === '-'"
                  color="thirdly"
                  size="55"
                  class="mb-2"
                >
                  <span class="text-h5 font-weight-bold">-</span>
                </v-avatar>
                <v-avatar
                  v-else-if="
                    banddata.bandData.scdState === 2 ||
                      banddata.bandData.scdState === 3
                  "
                  color="thirdly"
                  size="55"
                  class="mb-2"
                >
                  <v-icon color="black">
                    mdi-check-bold
                  </v-icon>
                </v-avatar>
                <v-avatar v-else color="thirdly" size="55" class="mb-2">
                  <v-icon color="black">
                    mdi-close-thick
                  </v-icon>
                </v-avatar>
                <v-list-item-title class="title-style"></v-list-item-title>
              </div>
            </v-list-item-content>
            <v-list-item-content>
              <div class="mx-auto text-center">
                <v-avatar color="thirdly" size="55" class="mb-2">
                  <span class="text-h5 font-weight-bold">{{
                    banddata.rssi
                  }}</span>
                </v-avatar>
                <v-list-item-title class="title-style"
                  ></v-list-item-title
                >
              </div>
            </v-list-item-content>
            <v-list-item-content>
              <div class="mx-auto text-center">
                <v-avatar color="thirdly" size="55" class="mb-2">
                  <span class="text-h5 font-weight-bold">{{
                    banddata.bandData.battery_level
                  }}</span>
                </v-avatar>
                <v-list-item-title class="title-style"
                  ></v-list-item-title
                >
              </div>
            </v-list-item-content>
            <v-list-item-content>
              <div class="mx-auto text-center">
                <v-avatar
                  v-if="banddata.bandData.activity === '-'"
                  color="thirdly"
                  size="55"
                  class="mb-2"
                >
                  <span class="text-h5 font-weight-bold">-</span>
                </v-avatar>
                <v-avatar v-else color="thirdly" size="55" class="mb-2">
                  <v-icon color="black">
                    {{ changeIcon(banddata.bandData.activity, false) }}
                  </v-icon>
                </v-avatar>
                <v-list-item-title class="title-style"></v-list-item-title>
              </div>
            </v-list-item-content>
          </v-list-item>
        </div>
        <div flat class="border-style rounded-lg mt-5">
          <v-list-item-title class="title-style-margin"
            > </v-list-item-title
          >
          <v-list-item>
            <v-list-item-content>
              <div class="mx-auto text-center">
                <v-avatar color="thirdly" size="55" class="mb-2">
                  <span class="text-h5 font-weight-bold">{{
                    banddata.bandData.walk_steps
                  }}</span>
                </v-avatar>
                <v-list-item-title class="title-style"></v-list-item-title>
              </div>
            </v-list-item-content>
            <v-list-item-content>
              <div class="mx-auto text-center">
                <v-avatar color="thirdly" size="55" class="mb-2">
                  <span class="text-h5 font-weight-bold">{{
                    banddata.bandData.run_steps
                  }}</span>
                </v-avatar>
                <v-list-item-title class="title-style"
                  ></v-list-item-title
                >
              </div>
            </v-list-item-content>
            <v-list-item-content>
              <div class="mx-auto text-center">
                <v-avatar color="thirdly" size="55" class="mb-2">
                  <span
                    v-if="banddata.bandData.h != '-'"
                    class="text-h5 font-weight-bold"
                    @click="modalView()"
                    >{{ (banddata.bandData.h / 100).toFixed(1) }}</span
                  >
                  <span v-else class="text-h5 font-weight-bold">-</span>
                </v-avatar>
                <v-list-item-title class="title-style"></v-list-item-title>
              </div>
            </v-list-item-content>
            <v-list-item-content>
              <div class="mx-auto text-center">
                <v-avatar color="thirdly" size="55" class="mb-2">
                  <span class="text-h5 font-weight-bold">{{
                    banddata.bandData.fall_detect
                  }}</span>
                </v-avatar>
                <v-list-item-title class="title-style"></v-list-item-title>
              </div>
            </v-list-item-content>
          </v-list-item>
        </div>
      </v-list>
      <v-list class="border-style rounded-lg mt-1">
        <div outlined class="border-style-top rounded-t-lg">
          <v-list-item-title class="title-style-margin"
            > </v-list-item-title
          >
          <v-list-item>
            <v-list-item-content>
              <v-list-item-title>BID</v-list-item-title>

              <v-list-item-subtitle
                v-text="userdata.bandinfo"
              ></v-list-item-subtitle>
            </v-list-item-content>
            <v-list-item-icon>
              <v-icon>
                mdi-chevron-right
              </v-icon>
            </v-list-item-icon>
          </v-list-item>
        </div>
        <div class="border-style-top rounded-0">
          <v-list-item-title class="title-style-margin"
            > </v-list-item-title
          >
          <v-list-item v-for="(item, i) in userdata.personalinfo" :key="i">
            <v-list-item-content>
              <v-list-item-title v-text="item.title"></v-list-item-title>

              <v-list-item-subtitle v-text="item.value"></v-list-item-subtitle>
            </v-list-item-content>
            <v-list-item-icon>
              <v-icon>
                mdi-chevron-right
              </v-icon>
            </v-list-item-icon>
          </v-list-item>
        </div>
        <div class="border-style rounded-b-lg">
          <v-list-item-title class="title-style-margin"
            ></v-list-item-title
          >
          <v-list-item v-for="(item, i) in userdata.managerinfo" :key="i">
            <v-list-item-content>
              <v-list-item-title v-text="item.name"></v-list-item-title>

              <v-list-item-subtitle
                v-text="item.permission"
              ></v-list-item-subtitle>
            </v-list-item-content>
            <v-list-item-icon>
              <v-icon>
                mdi-chevron-right
              </v-icon>
            </v-list-item-icon>
          </v-list-item>
        </div>
      </v-list>
      <v-bottom-sheet v-model="sheet">
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            v-bind="attrs"
            v-on="on"
            block
            outlined
            color="grey"
            class="mt-2"
          >
            <div class="black--text text-decoration-underline">
              
            </div>
          </v-btn>
        </template>
        <v-list>
          <v-subheader> </v-subheader>
          <v-list-item
            v-for="tile in tiles"
            :key="tile.title"
            @click="sheet = false"
            link
            :to="{ name: tile.to, query: { bandInfo: JSON.stringify(band) } }"
          >
            <v-list-item-avatar>
              <v-avatar size="32px" tile>
                <img
                  :src="require(`../assets/${tile.img}`)"
                  :alt="tile.title"
                />
              </v-avatar>
            </v-list-item-avatar>
            <v-list-item-title>{{ tile.title }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-bottom-sheet>
    </v-container>
  </div>
</template>

<script>
import Alert from "../components/Alert.vue";
import ModalView from "../components/Modal.vue";

export default {
  components: {
    Alert,
    ModalView,
  },
  props: {
    bandInfo: {
      type: String,
      default: "",
    },
  },
  data: () => ({
    isModalViewed: false,
    band: {
      bandinfo: {
        name: "",
      },
    },
    value: {
      title: "  ",
      subtitle: "",
      content: "   .  .",
      state: 1,
    },
    banddata: {
      bandData: {
        start_byte: "-",
        sample_count: "-",
        fall_detect: "-",
        battery_level: "-",
        hrConfidence: "-",
        spo2Confidence: "-",
        hr: "-",
        spo2: "-",
        motionFlag: "-",
        scdState: "-",
        activity: "-",
        walk_steps: "-",
        run_steps: "-",
        x: "-",
        y: "-",
        z: "-",
        t: "-",
        h: "-",
      },
      rssi: "-",
    },
    alertView: false,
    userdata: {
      bandinfo: "0x00",
      personalinfo: [
        {
          title: "",
          value: "",
        },
        {
          title: "",
          value: "1947 09 1",
        },
      ],
      managerinfo: [
        {
          name: "",
          permission: "staff",
        },
        {
          name: "",
          permission: "manager",
        },
      ],
    },
    sheet: false,

    tiles: [
      {
        img: "hr.svg",
        title: "",
        to: "hr",
      },
      { img: "spo2.svg", title: "", to: "spo2" },
      { img: "activity.svg", title: "", to: "activity" },
    ],
  }),
  async mounted() {
    this.changeJson();
    //await this.getBandUser();
    this.$session.$on("data", this.onDataHandler.bind(this));
  },
  created() {
    // this.check();
  },
  methods: {
    dialogEvent(check) {
      this.alertView = check;
      this.$router.go(-1);
    },
    closeModal() {
      this.isModalViewed = false;
    },
    changeJson() {
      this.band = JSON.parse(this.$route.query.bandInfo);
      this.userdata.bandinfo = this.band.bid;
      this.userdata.personalinfo[0].value = this.band.name;
      var date = new Date(this.band.birth);
      this.userdata.personalinfo[1].value = date.toLocaleDateString();
    },
    onDataHandler(data) {
      if (data.shortAddress === this.band.bid) {
        //console.log(data);
        // if (data.active === "false") {
        //   if(this.alertView===false) this.alertView = true;
        // } else {
        //   this.alertView = false;
        // }
        this.banddata = data;
      }
    },
    modalView() {
      this.isModalViewed = true;
    },
    async getBandUser() {
      try {
        var res = await this.$http.get("bands/userinfo/" + this.band.id);
        var user = res.data.data;
        for (var i = 0; i < user.length; i++) {
          res = await this.$http.get("user_table/groupinfo/" + user[i].id);
          var group = res.data.data;
          var groupstr = "";
          for (var j = 0; j < group.length; j++) {
            groupstr = groupstr + " " + group[j].groupname;
          }
          this.userdata.managerinfo.push({
            value: user[i].name,
            position: groupstr,
          });
        }
      } catch (e) {
        console.log(e);
      }
    },
    getUser(data) {
      console.log(data);
    },
    changeIcon(data, check) {
      if (check) {
        switch (data) {
          case 3:
            return "mdi-check-bold";
        }
      } else {
        switch (data) {
          case 0:
            return "mdi-human-handsdown";
          case 1:
            return "mdi-human-handsdown";
          case 2:
            return "mdi-walk";
          case 3:
            return "mdi-run";
          case 4:
            return "mdi-bike";
          case 5:
            return "mdi-run-fast";
        }
      }
    },
  },
};
</script>
<style lang="scss">
.border-style {
  border: 1px solid #d3d3d3;
}
.border-style-top {
  border-left: 1px solid #d3d3d3;
  border-right: 1px solid #d3d3d3;
  border-top: 1px solid #d3d3d3;
}
.border-style-bottom {
  border-left: 1px solid #d3d3d3;
  border-right: 1px solid #d3d3d3;
  border-bottom: 1px solid #d3d3d3;
}

.center {
  position: relative;
}
.icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.title-style {
  font-size: 0.875rem;
  color: black;
}
.title-style-margin {
  font-size: 0.875rem;
  color: black;
  margin-left: 8px;
  margin-top: 8px;
}
.modalview {
  max-width: 350px;
  max-height: 350px;
}
</style>
