<template>
  <div>
    <div class="primary top-background">
      <v-container fluid>
        <bar-chart
          v-if="$router.history.current.name === 'activity'"
          class="white chart-view"
          :datas="datedata"
          :bind="true"
        />
        <line-chart
          v-else-if="
            $router.history.current.name === 'hr' ||
              $router.history.current.name === 'spo2'
          "
          class="white chart-view"
          :datas="graphdata"
          :options="options"
          :bind="true"
        />
      </v-container>
    </div>
    <history class="mt-n4" />
  </div>
</template>

<script>
import LineChart from "../components/LineChart.vue";
import BarChart from "../components/BarChart.vue";
import History from "../components/History.vue";

export default {
  components: { LineChart, BarChart, History },
  methods: {
    onHistoryData(item) {
      try {
        this.datedata = item;
        this.graphdata = item.data;
      } catch (ex) {
        this.datedata = "";
        this.graphdata = [];
      }
    },
  },
  data() {
    return {
      datedata: "",
      graphdata: {
        //Data to be represented on x-axis

        datasets: [
          {
            backgroundColor: "#249EBF",
            borderWidth: 2,
            borderColor: "#249EBF",
            lineTension: 0,
            //Data to be represented on y-axis
            data: [],
            fill: false,
          },
        ],
      },
      graph: {
        //Data to be represented on x-axis

        datasets: [
          {
            backgroundColor: "#249EBF",
            borderWidth: 2,
            borderColor: "#249EBF",
            lineTension: 0,
            //Data to be represented on y-axis
            data: [],
            fill: false,
          },
        ],
      },
      //Chart.js options that controls the appearance of the chart
      options: {
        scales: {
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
              },
              gridLines: {
                display: true,
              },
            },
          ],
          xAxes: [
            {
              gridLines: {
                display: true,
              },
            },
          ],
        },
        legend: {
          display: false,
        },
        responsive: true,
        maintainAspectRatio: false,
      },
    };
  },
  mounted() {
    this.$session.selectBand = JSON.parse(this.$route.query.bandInfo);
    this.$session.$on("historydata", this.onHistoryData.bind(this));
  },
};
</script>

<style lang="scss">
.top-background {
  height: 35vh;
}
.chart-view {
  height: 30vh;
}
</style>
