<template>
  <v-card-text class="ml-n6">
    <v-row>
      <v-col>
        <v-card-subtitle class="subtitle-1 font-weight-bold"
          ></v-card-subtitle
        >
      </v-col>
      <div justify="right" align="right" class="list-item mt-4">
        <!-- <v-autocomplete
          v-model="sort"
          :items="items"
          justify="right"
          align="right"
          append-icon="mdi-menu-down"
          class="date-field mr-1"
          dense
        ></v-autocomplete> -->
        <v-select
          v-model="type"
          :items="items"
          justify="right"
          align="right"
          append-icon="mdi-menu-down"
          class="date-field mr-1"
          dense
          @change="typeSelect()"
        ></v-select>
      </div>
      <v-btn icon class="list-item mr-1" @click="moveDate(0)"
        ><v-icon dark>
          mdi-chevron-left
        </v-icon></v-btn
      >
      <v-btn icon class="list-item mr-n5" @click="moveDate(1)"
        ><v-icon dark>
          mdi-chevron-right
        </v-icon></v-btn
      >
    </v-row>
    <v-menu
      ref="menu1"
      v-model="menu1"
      :close-on-content-click="false"
      transition="scale-transition"
      offset-y
      max-width="290px"
      min-width="auto"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-text-field
          class="text-field mt-n8 ml-4"
          v-model="dateFormatted"
          persistent-hint
          readonly
          prepend-icon="mdi-calendar"
          v-bind="attrs"
          v-on="on"
        ></v-text-field>
      </template>
      <v-date-picker
        v-model="date"
        no-title
        :allowed-dates="allowedDates"
        :max="
          new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
            .toISOString()
            .substr(0, 10)
        "
        :min="minDate"
        @input="menu1 = false"
      ></v-date-picker>
    </v-menu>
    <v-timeline dense align-top>
      <v-timeline-item
        small
        fill-dot
        class="mt-n2"
        v-for="(item, i) in historyitems"
        :color="select === i ? 'green' : 'grey'"
        :key="i"
      >
        <div
          v-if="$router.history.current.name === 'activity'"
          class="ml-n4 "
          @click="timelineclick(i, item.date)"
        >
          <h3>
            {{ item.day }}
          </h3>
          <div>
            <v-row>
              <v-col cols="5">
                <strong></strong>
                <div class="text-caption">
                  {{ item.walk_steps }}
                </div>
              </v-col>
              <v-col>
                <strong></strong>
                <div class="text-caption">
                  {{ item.run_steps }}
                </div>
              </v-col>
            </v-row>
            <v-row>
              <hori-bar-chart
                class="sub-chart-view-hori"
                :datas="item.data"
                :bind="true"
              />
            </v-row>
          </div>
        </div>
        <div
          v-else-if="
            $router.history.current.name === 'hr' ||
              $router.history.current.name === 'spo2'
          "
          class="ml-n4 "
          @click="timelineclick(i, item)"
        >
          <h3>
            {{ item.date }}
          </h3>
          <div>
            <v-row>
              <v-col cols="5">
                <strong> - </strong>
                <div class="text-caption">
                  {{ item.minmax }}
                </div>
              </v-col>
              <v-col>
                <strong></strong>
                <div class="text-caption">
                  {{ Math.round(item.normal) }}
                </div>
              </v-col>
            </v-row>
            <v-row>
              <line-chart
                class="sub-chart-view"
                :datas="item.data"
                :options="options"
                :bind="true"
              />
            </v-row>
          </div>
        </div>
      </v-timeline-item>
    </v-timeline>
  </v-card-text>
</template>

<script>
import LineChart from "./LineChart.vue";
import HoriBarChart from "./HoriBarChart.vue";
export default {
  components: { LineChart, HoriBarChart },
  data: (vm) => ({
    date: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
      .toISOString()
      .substr(0, 10),
    dateFormatted: vm.formatDate(
      new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        .toISOString()
        .substr(0, 10)
    ),
    menu1: false,
    menu2: false,
    items: ["", "", ""],
    type: "",
    select: 0,
    historyitems: [],
    //allowedDates: ["2021-09-01", "2021-09-02"],
    //Chart.js options that controls the appearance of the chart
    options: {
      scales: {
        yAxes: [
          {
            ticks: {
              beginAtZero: true,
              display: false,
            },
            gridLines: {
              display: false,
            },
          },
        ],
        xAxes: [
          {
            gridLines: {
              display: false,
            },
          },
        ],
      },
      legend: {
        display: false,
      },
      responsive: true,
      maintainAspectRatio: false,
    },
    dateList: [],
    ad: [],
    minDate: null,
  }),

  computed: {},

  watch: {
    async date() {
      this.dateFormatted = this.formatDate(this.date);
    },
    $route() {
      if (this.date) {
        this.rangeDate(this.date);
      }
    },
  },
  async mounted() {
    await this.getAllowedDates();
  },
  methods: {
    formatDate(date) {
      if (!date) return null;
      this.rangeDate(date);

      return [this.dateList[0], this.dateList[this.dateList.length - 1]].join(
        " ~ "
      );
    },
    async rangeDate(val) {
      var computeDate = new Date(val);
      var today = new Date();
      today = today
        .toISOString()
        .substring(0, 10)
        .replaceAll("-", "");
      this.dateList = [];
      var temp = new Date(computeDate);
      temp.setDate(temp.getDate() - computeDate.getDay());

      for (var i = 0; i < 7; i++) {
        if (
          today -
            temp
              .toISOString()
              .substring(0, 10)
              .replaceAll("-", "") >=
          0
        ) {
          this.dateList.push(temp.toLocaleDateString("en-CA"));
        }
        temp.setDate(temp.getDate() + 1);
      }
      switch (this.$router.history.current.name) {
        case "hr":
          await this.vitalQueryDate();
          break;
        case "spo2":
          await this.vitalQueryDate();
          break;
        case "activity":
          await this.activityQueryDate();
          break;
      }
    },
    async vitalQueryDate() {
      await this.$http
        .post("sensordata/vital", {
          bid: JSON.parse(this.$route.query.bandInfo).bid,
          dataname: this.$router.history.current.name,
          days: this.dateList,
        })
        .then((res) => {
          this.historyitems = [];
          var result = res.data.data;
          for (var i = 0; i < result.length; i++) {
            console.log(result[i].date);
            this.historyitems.push({
              date: result[i].date,
              minmax: result[i].total.minmax,
              normal: result[i].total.normal,
              data: {
                //Data to be represented on x-axis
                labels: result[i].value.label,
                datasets: [
                  {
                    backgroundColor: "#249EBF",
                    borderWidth: 2,
                    borderColor: "#249EBF",
                    lineTension: 0,
                    //Data to be represented on y-axis
                    data: result[i].value.data,
                    fill: false,
                  },
                ],
              },
            });
          }
          this.timelineclick(0, this.historyitems[0]);
        })
        .catch((ex) => {
          console.log(ex);
        });
    },
    async activityQueryDate() {
      await this.$http
        .post("sensordata/activity/day", {
          bid: JSON.parse(this.$route.query.bandInfo).bid,
          days: [this.dateList[0], this.dateList[this.dateList.length - 1]],
        })
        .then((res) => {
          this.historyitems = [];
          var result = res.data.data;
          for (var i = 0; i < result.length; i++) {
            this.historyitems.push({
              day: result[i].day,
              date: result[i].d,
              walk_steps: result[i].walk_steps,
              run_steps: result[i].run_steps,
              data: {
                datasets: [
                  {
                    type: "horizontalBar",
                    label: "walk",
                    backgroundColor: "#5B9EF5",
                    data: [result[i].walk_steps],
                  },
                  {
                    type: "horizontalBar",
                    label: "run",
                    backgroundColor: "#7EE4AB",
                    data: [result[i].run_steps],
                  },
                ],
              },
            });
          }
          if (this.historyitems.length != 0)
            this.timelineclick(0, this.historyitems[0].date);
          else this.timelineclick(0, "");
        })
        .catch((ex) => {
          console.log(ex);
        });
    },
    async getAllowedDates() {
      try {
        var res = await this.$http.post("sensordata/date", {
          bid: JSON.parse(this.$route.query.bandInfo).bid,
        });
        this.ad = res.data.data;
        this.minDate = res.data.data[0];
      } catch (ex) {
        this.ad = [];
      }
    },
    parseDate(date) {
      if (!date) return null;
      return date;
    },
    timelineclick(i, item) {
      this.select = i;
      this.$session.$emit("historydata", item);
    },

    moveDate(direction) {
      var dateSplit = this.dateFormatted.split(" ~ ");
      var temp = "";
      temp = new Date(dateSplit[direction]);
      switch (direction) {
        case 0:
          temp.setDate(temp.getDate() - 1);
          break;
        case 1:
          temp.setDate(temp.getDate() + 1);
          break;
      }

      this.date = temp.toISOString().substr(0, 10);
    },
    typeSelect() {
      //,  query type date ,  
      console.log(this.type);
    },
    allowedDates(val) {
      for (var i = 0; i < this.ad.length; i++) {
        if (this.ad[i] == val) {
          return val;
        }
      }
    },
  },
};
</script>
<style lang="scss">
.text-field {
  width: 230px;
}
.date-field {
  width: 80px;
}
.list-item {
  // [jk]
  vertical-align: center;
}
.sub-chart-view {
  width: 100%;
  height: 120px;
}
.sub-chart-view-hori {
  width: 100%;
  height: 80px;
}
</style>
