<script>
import { Bar } from "vue-chartjs";

export default {
  extends: Bar,
  props: ["datas"],
  watch: {
    async datas() {
      await this.activityQuery();
    },
  },
  methods: {
    async activityQuery() {
      if (this.datas === "") {
        this.renderChart(this.data, this.options);
      } else {
        await this.$http
          .post("sensordata/activity/week", {
            bid: this.$session.selectBand.bid,
            date: this.datas,
          })
          .then((res) => {
            console.log(res.data.data);
            this.renderChart(
              {
                labels: res.data.data.date,
                datasets: [
                  {
                    type: "bar",
                    label: "walk",
                    backgroundColor: "#5B9EF5",
                    data: res.data.data.walk_steps,
                  },
                  {
                    type: "bar",
                    label: "run",
                    backgroundColor: "#7EE4AB",
                    data: res.data.data.run_steps,
                  },
                ],
              },
              this.options
            );
          })
          .catch((e) => {
            console.log(e);
            this.renderChart(this.data, this.options);
          });
      }
    },
  },
  async mounted() {
    await this.activityQuery();
  },
  data() {
    return {
      data: {
        datasets: [
          {
            type: "bar",
            label: "walk",
            backgroundColor: "#5B9EF5",
            data: [],
          },
          {
            type: "bar",
            label: "run",
            backgroundColor: "#7EE4AB",
            data: [],
          },
        ],
      },
      options: {
        scales: {
          yAxes: [
            {
              stacked: true,
            },
          ],
          xAxes: [
            {
              stacked: true,
            },
          ],
        },
        legend: {
          display: true,
        },
        responsive: true,
        maintainAspectRatio: false,
      },
    };
  },
};
</script>
