<template>
  <div>
    <v-card flat height="120" class="primary rounded-0 mx-auto"> </v-card>
    <v-card
      flat
      width="100"
      height="100"
      class="center secondary mt-n13 mx-auto"
      ><v-icon size="85" color="black" class="icon">
        mdi-account-circle
      </v-icon>
    </v-card>
    <v-card flat>
      <v-card-text class="display-1 text-center font-weight-bold">
        {{ account.name }}
      </v-card-text>
    </v-card>
    <v-container fluid>
      <v-list outlined class="rounded-lg">
        <div>
          <v-list-item-title class="title-style-margin"
            > </v-list-item-title
          >
          <v-list-item v-for="(item, i) in user" :key="i">
            <v-list-item-content>
              <v-list-item-title v-text="item.title"></v-list-item-title>

              <v-list-item-subtitle v-text="item.value"></v-list-item-subtitle>
            </v-list-item-content>
            <v-list-item-icon>
              <v-icon>
                mdi-chevron-right
              </v-icon>
            </v-list-item-icon>
          </v-list-item>
        </div>
      </v-list>
      <v-list outlined class="rounded-lg mt-5">
        <div>
          <v-list-item-title class="title-style-margin"
            > </v-list-item-title
          >
          <div v-for="(item, i) in bands" :key="i">
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title
                  class="font-weight-bold ml-4"
                  v-text="i + 1"
                ></v-list-item-title>
              </v-list-item-content>
              <v-list-item-content class="ml-n16">
                <v-list-item-title v-text="item.bid"></v-list-item-title>
              </v-list-item-content>
              <v-list-item-content class="ml-n16">
                <v-list-item-title v-text="item.name"></v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-divider class="mx-3" />
          </div>
        </div>
      </v-list>
    </v-container>
  </div>
</template>

<script>
export default {
  props: {
    accountProp: {
      type: String,
      default: "",
    },
  },
  data: () => ({
    user: [
      {
        title: "",
        value: "",
      },
      {
        title: "UID",
        value: 3000,
      },
      {
        title: " ",
        value: "U0000",
      },
      {
        title: "",
      },
      {
        title: "",
        value: "2021. 08. 20",
      },
      {
        title: "",
        value: "",
      },
    ],
    bands: [
      {
        bid: 1,
        name: "",
      },
      {
        bid: 2,
        name: "",
      },
    ],
    account: [],
  }),
  async mounted() {
    this.changeJson();
    //await this.getBandUser();
    //this.$session.$on("data", this.onDataHandler.bind(this));
  },

  created() {
    // this.check();
  },
  methods: {
    changeJson() {
      this.account = JSON.parse(this.accountProp);
      this.user[0].value = this.account.name;
      this.user[1].value = this.account.uid;
    },
  },
};
</script>
<style lang="scss">
.border-style {
  border: 1px solid #d3d3d3;
}
.border-style-top {
  border-left: 1px solid #d3d3d3;
  border-right: 1px solid #d3d3d3;
  border-top: 1px solid #d3d3d3;
}
.border-style-bottom {
  border-left: 1px solid #d3d3d3;
  border-right: 1px solid #d3d3d3;
  border-bottom: 1px solid #d3d3d3;
}

.center {
  position: relative;
}
.icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.title-style {
  font-size: 0.875rem;
  color: black;
}
.title-style-margin {
  font-size: 0.875rem;
  color: black;
  margin-left: 8px;
  margin-top: 8px;
}
</style>
