import Vue from "vue";
import VueRouter from "vue-router";
import BandInfo from "@/views/BandInfo";
import BandList from "@/views/BandList";
import Login from "@/views/Login";
import AccountList from "@/views/AccountList";
import AccountInfo from "@/views/AccountInfo";
import HistoryList from "@/views/HistoryList";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Login",
    component: Login,
    props: true,
  },
  {
    path: "/bandlist",
    name: "Band List",
    component: BandList,
    props: true,
  },
  {
    path: "/bandinfo",
    name: "Band Info",
    component: BandInfo,
    props: true,
  },
  {
    path: "/userlist",
    name: "User List",
    component: AccountList,
    props: true,
  },
  {
    path: "/userinfo",
    name: "User Info",
    component: AccountInfo,
    props: true,
  },
  {
    path: "/historylist/hr",
    name: "hr",
    component: HistoryList,
  },
  {
    path: "/historylist/spo2",
    name: "spo2",
    component: HistoryList,
  },
  {
    path: "/historylist/activity",
    name: "activity",
    component: HistoryList,
  },
];

const router = new VueRouter({
  mode: "history",
  routes,
});

export default router;
