<template>
  <div v-if="nav.view">
    <v-app-bar color="primary" dark flat class="text-center">
      <v-app-bar-nav-icon @click.stop="drawer = !drawer">
        <v-img src="../../src/assets/menu.svg" max-height="24" max-width="24">
        </v-img>
      </v-app-bar-nav-icon>
      <v-spacer></v-spacer>
      <v-toolbar-title
        v-if="titleview"
        class=" headline font-weight-bold text-uppercase"
        >{{ title }}</v-toolbar-title
      >
      <v-menu v-if="historyview" offset-y>
        <template v-slot:activator="{ on, attrs }">
          <v-toolbar-title
            v-model="title"
            v-bind="attrs"
            v-on="on"
            append-icon="mdi-menu-down"
            v-text="title"
            class=" headline font-weight-bold text-uppercase text-decoration-underline"
          ></v-toolbar-title>
        </template>
        <v-list>
          <v-list-item
            v-for="(item, index) in historyitems"
            :key="index"
            router-link
            :to="{
              name: item.to,
              query: { bandInfo: JSON.stringify($session.selectBand) },
            }"
            replace
          >
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
      <v-spacer></v-spacer>
      <v-btn icon @click="back()">
        <v-icon v-if="titleview">mdi-plus-thick</v-icon>
        <v-icon v-else>mdi-undo-variant</v-icon>
        <!-- <v-icon>mdi-plus-thick</v-icon> -->
      </v-btn>
      <!-- <template v-slot:extension v-if="check.search.view">
        <v-text-field
          dense
          clearable
          flat
          solo-inverted
          hide-details
          v-model="searchValue"
          append-icon="mdi-magnify"
        ></v-text-field>
      </template> -->
    </v-app-bar>
    <v-navigation-drawer
      v-model="drawer"
      absolute
      temporary
      :height="windowHeight"
    >
      <template>
        <v-list-item two-line link>
          <v-list-item-avatar class="secondary">
            <span class="white--text">{{
              $session.user.name.substr(0, 2)
            }}</span>
          </v-list-item-avatar>

          <v-list-item-content>
            <v-list-item-title>{{ $session.user.name }}</v-list-item-title>
            <v-list-item-subtitle>{{
              $session.user.username
            }}</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </template>
      <v-divider></v-divider>

      <v-list-item>
        <v-list-item-content>
          <v-list-item-subtitle>
            Band
          </v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
      <v-list dense>
        <v-list-item v-for="item in items" :key="item.title" link :to="item.to">
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
      <template v-slot:append>
        <div class="pa-2">
          <v-btn depressed block class="primary" @click="logout()">
            Logout
          </v-btn>
        </div>
      </template>
    </v-navigation-drawer>
  </div>
</template>

<script>
export default {
  name: "Menu",
  props: {
    view: {},
  },
  data() {
    return {
      drawer: false,
      items: [
        { title: " ", icon: "mdi-watch-variant", to: "/bandlist" },
        { title: " ", icon: "mdi-account-circle", to: "/userlist" },
      ],
      historyitems: [
        { title: "", to: "hr" },
        { title: "", to: "spo2" },
        { title: "", to: "activity" },
      ],
      nav: {
        list: ["Login"],
        view: true,
      },
      appbaricon: ["Band Info", "User Info", "hr", "spo2", "activity"],
      searchValue: null,
      title: null,
      historyview: false,
      titleview: true,
      windowHeight: null,
    };
  },
  mounted() {
    window.addEventListener("resize", () => {
      this.windowHeight = window.innerHeight;
    });
    //this.getBandList();
  },
  methods: {
    init() {
      this.title = this.$route.name;
      if (this.nav.list.includes(this.title)) this.nav.view = false;
      else {
        this.nav.view = true;
        this.historyview = false;
        this.titleview = true;
        if (this.appbaricon.includes(this.title)) {
          this.titleview = false;
          switch (this.$route.path) {
            case "/historylist/hr":
              this.title = "";
              this.historyview = true;
              break;
            case "/historylist/spo2":
              this.title = "";
              this.historyview = true;
              break;
            case "/historylist/activity":
              this.title = "";
              this.historyview = true;
              break;
          }
        }
      }
    },

    logout() {
      this.drawer = !this.drawer;
      this.$session.releaseSession();
      this.$router.push("/");
    },

    back() {
      this.$router.go(-1);
    },
  },
  created() {
    this.init();
  },
  watch: {
    $route() {
      this.init();
    },
  },
};
</script>
