<template>
  <v-data-iterator
    :items="bandinfo"
    :items-per-page.sync="itemsPerPage"
    :page.sync="page"
    :search="search"
    :no-data-text="nodata"
    hide-default-footer
  >
    <template v-slot:header>
      <v-toolbar dark color="primary" flat>
        <v-container>
          <v-text-field
            v-model="search"
            clearable
            flat
            solo-inverted
            hide-details
            label="Search"
            dense
            append-icon="mdi-magnify"
          ></v-text-field>
        </v-container>
      </v-toolbar>
    </template>

    <template v-slot:default="props">
      <v-container fluid>
        <v-list>
          <template v-for="(band, index) in props.items">
            <v-card :key="index" flat outlined tile>
              <v-list-item
                action
                :key="band.alias"
                @click="listClick(bandinfo[index])"
              >
                <v-list-item-icon class="list-item">
                  <v-icon
                    small
                    :color="
                      band.bandvalue.active === 'true' ? 'secondary' : 'grey'
                    "
                    >mdi-circle</v-icon
                  >
                </v-list-item-icon>

                <v-list-item-action class="list-item ml-n5 mr-2">
                  <v-list-item-title class="text-style">{{
                    band.id
                  }}</v-list-item-title>
                </v-list-item-action>
                <v-list-item-content class="list-item">
                  <v-list-item-title class="text-style">{{
                    band.name
                  }}</v-list-item-title>
                </v-list-item-content>
                <v-list-item-icon class="list-item">
                  <v-icon
                    v-if="band.bandvalue.bandData.scdState === ''"
                    color="grey"
                  >
                    mdi-watch-variant
                  </v-icon>
                  <v-icon
                    v-else-if="
                      band.bandvalue.bandData.scdState === 3 ||
                        band.bandvalue.bandData.scdState === 2
                    "
                    color="black"
                  >
                    mdi-watch-variant
                  </v-icon>
                  <v-icon v-else color="red">
                    mdi-watch-variant
                  </v-icon>

                  <v-icon v-if="band.bandvalue.rssi === ''" color="grey">
                    mdi-signal-cellular-outline
                  </v-icon>
                  <v-icon v-else-if="band.bandvalue.rssi > -30" color="black">
                    mdi-signal-cellular-3
                  </v-icon>
                  <v-icon v-else-if="band.bandvalue.rssi > -50" color="black">
                    mdi-signal-cellular-2
                  </v-icon>
                  <v-icon v-else-if="band.bandvalue.rssi > -75" color="black">
                    mdi-signal-cellular-1
                  </v-icon>
                  <v-icon v-else color="red">
                    mdi-signal-cellular-outline
                  </v-icon>

                  <v-icon
                    v-if="band.bandvalue.bandData.battery_level === ''"
                    color="grey"
                  >
                    mdi-battery-high
                  </v-icon>
                  <v-icon
                    v-else-if="band.bandvalue.bandData.battery_level > 80"
                    color="black"
                  >
                    mdi-battery-high
                  </v-icon>
                  <v-icon
                    v-else-if="band.bandvalue.bandData.battery_level > 40"
                    color="black"
                  >
                    mdi-battery-medium
                  </v-icon>
                  <v-icon
                    v-else-if="band.bandvalue.bandData.battery_level > 15"
                    color="black"
                  >
                    mdi-battery-low
                  </v-icon>
                  <v-icon v-else color="red">
                    mdi-battery-outline
                  </v-icon>

                  <div class="align-style">
                    {{ band.bandvalue.bandData.battery_level }}%
                  </div>
                </v-list-item-icon>

                <!-- <v-list-item-icon class="list-item">
                <v-btn
                  depressed
                  block
                  class="primary"
                  @click="log(bands[index])"
                  ></v-btn
                >
              </v-list-item-icon> -->
              </v-list-item>
            </v-card>
          </template>
        </v-list>
      </v-container>
    </template>
    <template v-slot:footer>
      <v-row class="" align="center" justify="center">
        <v-spacer></v-spacer>

        <span
          class="mr-4
            grey--text"
        >
          Page {{ page }} of {{ numberOfPages }}
        </span>

        <v-icon class="mr-1" @click="formerPage">mdi-chevron-left</v-icon>

        <v-icon class="ml-1 mr-4" @click="nextPage">mdi-chevron-right</v-icon>
      </v-row>
    </template>
  </v-data-iterator>
</template>

<script>
export default {
  // props: {
  //   userInfo: {
  //     type: String,
  //     default: "",
  //   },
  // },
  async created() {
    if (this.$session.user != null) {
      this.$session.$on("data", this.onDataHandler.bind(this));
      var res = await this.$session.historyCheck();
      if (res) await this.getBandList();
      else this.$router.push("/");
    } else {
      this.$router.push("/");
    }
  },
  async mounted() {
    // this.changeJson();
    // this.$session.$on("data", this.onDataHandler.bind(this));
    // await this.getBandList();
  },
  data: () => ({
    bandinfo: [],
    search: "",
    nodata: "",
    bandvalue: {
      bandData: {
        start_byte: "",
        sample_count: "",
        fall_detect: "",
        battery_level: "",
        hrConfidence: "",
        spo2Confidence: "",
        hr: "",
        spo2: "",
        motionFlag: "",
        scdState: "",
        activity: "",
        walk_steps: "",
        run_steps: "",
        x: "",
        y: "",
        z: "",
        t: "",
        h: "",
      },
      rssi: "",
      extAddress: {
        row: 0,
      },
    },
    itemsPerPageArray: [5, 10, 15, 20],
    page: 1,
    itemsPerPage: 10,
  }),
  methods: {
    // changeJson() {
    //   this.user = JSON.parse(this.userInfo);
    // },
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    },
    async getBandList() {
      try {
        var band = null;
        var bands = null;
        var group = await this.$http.get(
          "users/groupinfo/" + this.$session.user.id
        );
        group = group.data.data;
        console.log(group[0].permission);
        switch (group[0].permission) {
          case 0:
            band = await this.$http.get("bands/list");
            bands = band.data.data;
            break;
          case 1:
            band = await this.$http.get(
              "users/bandinfo/" + this.$session.user.id
            );
            bands = band.data.data;
            break;
          case 2:
            break;
        }
        for (var i = 0; i < bands.length; i++) {
          this.bandinfo.push({
            alias: bands[i].alias,
            bid: bands[i].bid,
            birth: bands[i].birth,
            created: bands[i].created,
            gender: bands[i].gender,
            id: bands[i].id,
            name: bands[i].name,
            bandvalue: this.bandvalue,
          });
          console.log(bands[i]);
        }
      } catch (ex) {
        console.log(ex);
      }
    },
    onDataHandler(data) {
      //console.log(data);
      if (data.active === "false") {
        this.bandinfo[data.shortAddress - 1].bandvalue = this.bandvalue;
      } else {
        this.bandinfo[data.shortAddress - 1].bandvalue = data;
      }
    },

    async listClick(bandinfo) {
      console.log(bandinfo);
      //console.log(bandinfo);
      if (localStorage.getItem("SESSION") != null) {
        var res = await this.$session.historyCheck();
        if (res.data != "") {
          this.$session.selectBand = bandinfo;
          this.$router.push({
            name: "Band Info",
            query: { bandInfo: JSON.stringify(bandinfo) },
          });
        } else {
          this.$router.push("/");
        }
      } else {
        console.log("null");
        this.$session.logoutUser();
        this.$router.push("/");
      }
    },
  },
  computed: {
    numberOfPages() {
      return Math.ceil(this.bandinfo.length / this.itemsPerPage);
    },
    filteredKeys() {
      return this.keys.filter((key) => key !== "Name");
    },
  },
};
</script>
<style lang="scss">
.list-item {
  align-self: center; // [jk]
}
.text-style {
  font-size: 1.2rem;
}
.align-style {
  width: 40px;
}
</style>
