const state = {
  message: "Hello",
};
const mutations = {
  changeMessage(state, newMsg) {
    state.message = newMsg;
  },
};
const actions = {
  callMutation({ state, commit }, { newMsg }) {
    console.log(state);
    commit("changeMessage", newMsg);
  },
};
const getters = {
  getMsg(state) {
    return `${state.message} => Length : ${state.message.length}`;
  },
};

export default {
  state,
  mutations,
  actions,
  getters,
};
