<script>
import { HorizontalBar } from "vue-chartjs";

export default {
  extends: HorizontalBar,
  props: ["datas"],
  watch: {
    datas() {
      this.renderChart(this.datas, this.options);
    },
  },
  mounted() {
    // Overwriting base render method with actual data.
    this.renderChart(this.datas, this.options);
  },
  data() {
    return {
      options: {
        scales: {
          yAxes: [
            {
              stacked: true,
            },
          ],
          xAxes: [
            {
              stacked: true,
              categoryPercentage: 0.5,
              barPercentage: 1,
            },
          ],
        },
        legend: {
          display: true,
        },
        responsive: true,
        maintainAspectRatio: false,
      },
    };
  },
};
</script>
