<template>
  <ApexChart type="line" ref="chart" :options="options" :series="series" />
</template>

<script>
var lastDate = 0;
var dataArray = [];
var TICKINTERVAL = 86400000;
var interval = null;

import ApexChart from "vue-apexcharts";
export default {
  props: ["datas"],
  watch: {
    datas() {
      console.log("------streamingchart-----");
    },
  },
  components: {
    ApexChart,
  },
  data: () => ({
    modalView: true,
    banddata: null,
    me: null,
    options: {
      chart: {
        id: "realtime",
        height: 350,
        type: "line",
        animations: {
          enabled: true,
          easing: "linear",
          dynamicAnimation: {
            speed: 1000,
          },
        },
        toolbar: {
          show: false,
        },
        zoom: {
          enabled: false,
        },
      },
      dataLabels: {
        enabled: false,
      },
      stroke: {
        curve: "smooth",
        width: [4, 4],
      },
      markers: {
        size: 0,
      },
      xaxis: {
        type: "datetime",
        range: 40000,
      },

      legend: {
        show: false,
      },
    },
    series: [
      {
        data: dataArray.slice(),
      },
    ],
  }),

  mounted() {
    this.$session.$on("data", this.onDataHandler.bind(this));
  },
  methods: {
    realChart() {
      try {
        var me = this;
        me.$refs.chart.updateSeries([
          {
            data: dataArray,
          },
        ]);
      } catch (e) {
        window.clearInterval(interval);
      }
    },
    onDataHandler(data) {
      if (data.shortAddress === this.$session.selectBand.bid) {
        if (this.banddata != null) {
          if (this.banddata.shortAddress != data.shortAddress) {
            dataArray = [];
          }
        }

        this.banddata = data;
        this.getNewSeries(lastDate, data.bandData.h / 100);
      }
    },
    getNewSeries(baseval, yrange) {
      const curr = new Date();

      var newDate = baseval + TICKINTERVAL;
      newDate = curr;

      //if (yrange < 0) start = lastDate;
      dataArray.push({
        x: curr,
        y: yrange,
      });
      lastDate = newDate;
      this.realChart();
    },
  },
};
</script>
